
#container[data-v-5f2b3fc8]{
  width:100%;
  height:100%;
  position: relative;
  padding-bottom: 100px;
}

/* 音乐列表 */
.music-list[data-v-5f2b3fc8]{
  width:100%;
  height:100%;
}
.list-head[data-v-5f2b3fc8]{
  height:3em;
  line-height: 3em;
  background-color: #f2f2f6;
  margin:0 auto;
  text-align: justify;
  width:100%;
  overflow: hidden;
}
.music-list-item[data-v-5f2b3fc8]:after,.list-head[data-v-5f2b3fc8]:after{
  display: inline-block;
  content: '';
  width:100%;
  overflow: hidden;
}
.list-head span[data-v-5f2b3fc8]{
  text-align: center;
  display: inline-block;
  width:27%;
  height:2em;
  line-height:2em;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.music-list-item[data-v-5f2b3fc8]:nth-child(even){
  height:2em;
  line-height: 2em;
  background-color: #f2f2f6;
  color:#666;
  margin:0 auto;
  text-align: justify;
}
.music-list-item[data-v-5f2b3fc8]:nth-child(odd){
  height:2em;
  line-height: 2em;
  background-color: #fff;
  color:#666;
  margin:0 auto;
  text-align: justify;
}
.music-list-item span[data-v-5f2b3fc8]{
  text-align: center;
  display: inline-block;
  width:27%;
  height:2em;
  line-height:2em;
  cursor:pointer;
  text-overflow: ellipsis;
  white-space: nowrap;
  /*  暂时这样设置 */
  overflow: hidden;
}
.list-head span[data-v-5f2b3fc8]:first-child,.music-list-item span[data-v-5f2b3fc8]:first-child{
  width:10%;
}


/* 分类列表 */
.classify-list[data-v-5f2b3fc8]{
  position: absolute;
  top:0;
  left:-30%;
  width:30%;
  background-color: #fff;
  border:1px solid #ccc;
  z-index: 2;
  padding:1em 1em;
  line-height: 2em;
  display: none;
}
.classify-item[data-v-5f2b3fc8]{
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 100%;
  overflow: hidden;
  cursor: pointer;
}
.classify-item.select[data-v-5f2b3fc8]{
  background-color: #eee;
}
.classify-list.entered[data-v-5f2b3fc8]{
  -webkit-animation: entered-data-v-5f2b3fc8 0.3s ease;
          animation: entered-data-v-5f2b3fc8 0.3s ease;
  -webkit-animation-fill-mode : forwards;
          animation-fill-mode : forwards;
  display:block;
}
@-webkit-keyframes entered {
from{  left:-30%
}
to{  left:0;
}
}
@keyframes entered-data-v-5f2b3fc8 {
from{  left:-30%
}
to{  left:0;
}
}
.music-list-items[data-v-5f2b3fc8]{
  padding-bottom: 60px;
}

/* 手机横屏 */
@media only screen and (min-width:500px) {
}

/* ipad */
@media only screen and (min-width:760px) {
.classify-list[data-v-5f2b3fc8]{
    width:20%;
    position: relative;
    left:0;
    top:0;
    float: left;
    box-sizing: border-box;
}
.music-list[data-v-5f2b3fc8]{
    width:78%;
    float: left;
    box-sizing: border-box;
    margin-left: 2%;
}
.classify-list[data-v-5f2b3fc8]{
    display: block;
    margin-left: 0;
}
}

/* 电脑屏幕,固定宽度 */
@media only screen and (min-width:1024px) {
#container[data-v-5f2b3fc8]{
    width:1024px;
    margin: 0 auto;
}
}



