
html,body,div,span,header,footer,h1,h2,h3,h4,h5,h6,a,img,ul,li,nav{
  padding:0;
  margin:0;
}
html,body{
  width: 100%;
  height:100%;
}
a{
  text-decoration: none;
}
#app{
  width:100%;
  height:100%;
}
/* 头部 */
.header{
  width:100%;
  height:3em;
  line-height: 3em;
  background-color: #000;
  color:#fff;
  z-index: 10;
}
.logo{
  font-size:1.2em;
  font-weight: bold;
  margin-left: 1em;
  display: none;
}
.header-music-list{
  color:#fff;
  display: inline-block;
  margin-left:1em;
  height:3em;
  padding: 0 1em;
  cursor: pointer;
  font-size:0.5em;
}
.header-play-list{
  color:#fff;
  display: inline-block;
  margin-left:1em;
  height:3em;
  padding: 0 1em;
  cursor: pointer;
  font-size:0.5em;
}
.login-box{
  float:right;
  margin-right:1em;
  font-size: 0.5em;
}
.login-box span{
  cursor: pointer;
}
.spinner{
  width:20%;
  position: absolute;
  right:0;
  top:3em;
  z-index: 2;
  color:#000;
  background-color: #fff;
  list-style-type: none;
  border:1px solid #aaa;
}
.spinner li{
  padding-left: 10%;
}
.spinner li:hover{
  background-color: #eee;
}

/* 播放列表 */
.playList{
  width:100%;
  height:50%;
  overflow-y: scroll;
  position: absolute;
  background-color: #000;
  opacity: 0.8;
  top:3em;
  left:0;
  z-index: 100;
  display:none;
}
.playList.show{
  display: block;
  -webkit-animation: entered 0.3s;
          animation: entered 0.3s;
}
@-webkit-keyframes entered {
from{  top:-50%
}
to{  top:3em
}
}
@keyframes entered {
from{  top:-50%
}
to{  top:3em
}
}
.listhd{
  width:80%;
  margin:0 auto;
  height:2.5em;
  line-height:2.5em;;
  color:#fff;
}
.listhd .clear{
  float:right;
  cursor: pointer;
}
.playList-item{
  height:1.5em;
  width:80%;
  margin:0 auto;
  opacity: 0.5;
  color:#fff;
}
.playList-item .wrap{
  width:20%;
  display: inline-block;
  text-overflow: ellipsis;
  white-space: nowrap;
  vertical-align: top;
}
.playList-item .currentPlay{
  float: right;
}
.playList-item .details{
  width:70%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}



/* 手机横屏 */
@media only screen and (min-width:500px) {
.login-box,.header-music-list,.header-play-list{
    font-size: 1em;
}
}

/* ipad */
@media only screen and (min-width:760px) {
.header-music-list{
    display: none;
}
.logo{
    display: inline;
}
.playList{
    width:700px;
    left:50%;
    margin-left:-300px;
}
}

/* 电脑屏幕,固定宽度 */
@media only screen and (min-width:1024px) {
.container{
    width:1000px;
    margin: 0 auto;
}
}



