







































































































































































































/*
    播放音乐面板
*/
#MusicPanel[data-v-658aeec8]{
  position: fixed;
  width:100%;
  height:3em;
  background-color: #000;
  bottom:0;
  z-index: 10;
}
.wrap[data-v-658aeec8]{
  width:100%;
  height:100%;
}
.btns[data-v-658aeec8]{
  float:left;
  display: inline-block;
  margin-left: 1em;
  height:100%;
  padding-top:5px;
}
/*上一首*/
.btns span[data-v-658aeec8]{
  display: inline-block;
  background: url("/api/public/images/playbar.png") no-repeat;
}
.btns .last[data-v-658aeec8]{
  background-position: 0 -130px;
  width:28px;
  height:28px;
  margin-bottom:3px;
}
.btns .last[data-v-658aeec8]:hover{
  background-position: -30px -130px;
}

/*播放*/
.btns .play[data-v-658aeec8]{
  background-position: 0px -204px;
  width:36px;
  height:36px;
}
.btns .play[data-v-658aeec8]:hover{
  background-position: -40px -204px;
}

/*暂停*/
.btns .stop[data-v-658aeec8]{
  background-position: 0px -165px;
  width:36px;
  height:36px;
}
.btns .stop[data-v-658aeec8]:hover{
  background-position: -40px -165px;
}

/*下一首*/
.btns .next[data-v-658aeec8]{
  background-position: -80px -130px;
  width:28px;
  height:28px;
  margin-bottom:3px;
}
.btns .next[data-v-658aeec8]:hover{
  background-position: -110px -130px;
}

/*图片*/
#MusicPanel .pic[data-v-658aeec8]{
  float:left;
  width:34px;
  height:34px;
  overflow: hidden;
  margin: 7px 7px auto 15px;
  background-color: #aaa;
  display: none;
}
#MusicPanel .pic img[data-v-658aeec8]{
  width:34px;
  height:34px;
}

/*进度条*/
.progress[data-v-658aeec8]{
  display: inline-block;
  width:55%;
  height:34px;
  margin-left:0.7em;
  float:left;
  margin-top:7px;
}
.music-word[data-v-658aeec8]{
  color:#fff;
  font-size: 0.9em;
  height:19px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.music-play[data-v-658aeec8]{
  width:100%;
  height:3px;
  background-color: #aaa;
  margin-top:5px;
}
.play-btn[data-v-658aeec8]{
  width: 0;
  height: 3px;
  background-color: red;
}
.play-btn .now[data-v-658aeec8]{
  width:10px;
  height:10px;
  background-color: #fff;
  display: inline-block;
  float: right;
  margin-top: -4px;
  border-radius: 50%;
}

/* 显示歌词面板 */
.lyric-btn[data-v-658aeec8]{
  width:2.5em;
  height:2.5em;
  position: fixed;
  right:0;
  bottom: 4em;
  border-radius: 0.5em;
  background-color: #aaa;
  overflow: hidden;
}
.lyric-btn .btn-pic[data-v-658aeec8]{
  width:2.5em;
  height:2.5em;
}

/* 进度时间 */
.progress-num[data-v-658aeec8]{
  float: left;
  color:#fff;
  margin-top:15px;
  margin-left:15px;
  font-size: 0.9em;
  display: none;
}

/* 手机横屏 */
@media only screen and (min-width:500px) {
.progress-num[data-v-658aeec8],#MusicPanel .pic[data-v-658aeec8]{
    display: inline-block;
}
.lyric-btn[data-v-658aeec8]{
    display: none;
}
}

/* ipad  */
@media only screen and (min-width:760px) {
}
/* 电脑屏幕,固定宽度 */
@media only screen and (min-width:1024px) {
.wrap[data-v-658aeec8]{
    width:1024px;
    margin:0 auto;
}
}



